<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入姓名" class="w_200" v-model="searchName"></el-input>
                <div class="m_l_20">
                    <el-input placeholder="请输入ID" class="w_200" v-model="searchId"></el-input>
                </div>
                <div class="m_l_20">
                    <el-button @click="search" icon="search">搜索</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="id" label="ID">
                    </el-table-column>
                    <el-table-column align="center" prop="userName" label="姓名">
                    </el-table-column>
                    <el-table-column align="center" prop="userPhone" label="手机">
                    </el-table-column>
                    <el-table-column align="center" prop="gender" label="性别" :formatter="formatGender">
                    </el-table-column>
                    <el-table-column align="center" prop="driving" label="驾龄">
                    </el-table-column>
                    <el-table-column align="center" prop="createAt.time" label="注册时间" width="110" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="最后一次登录时间">
                        <template scope="scope">
                            {{getTime(scope.row.updateAt && scope.row.updateAt.time)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="loginCount" label="登录次数">
                    </el-table-column>
                    <el-table-column align="center" prop="consume" label="消费次数">
                    </el-table-column>
                    <el-table-column align="center" prop="money" label="消费金额">
                    </el-table-column>
                    <el-table-column align="center" prop="profit" label="利润">
                    </el-table-column>
                    <el-table-column align="center" prop="profitRate" label="利润率">
                    </el-table-column>
                    <el-table-column align="center" prop="creditScore" label="信用分">
                    </el-table-column>
                    <el-table-column align="center" prop="consumeIntegration" label="消费积分">
                    </el-table-column>
                    <el-table-column align="center" prop="grade" label="会员等级">
                    </el-table-column>
                    <el-table-column align="center" prop="balance" label="账户余额">
                    </el-table-column>
                    <el-table-column align="center" prop="cashbalance" label="现金余额">
                    </el-table-column>
                    <el-table-column align="center" prop="givebalance" label="赠送余额">
                    </el-table-column>
                    <!-- <el-table-column align="center" prop="userType" label="用户身份">
                        <template  scope="scope">
                                <p :class="{'green': scope.row.userType == 2, 'blue': scope.row.userType == 1}">
                                    <span>
                                        {{formatUserType(scope.row)}}
                                    </span>
                                </p>
                        </template>
                    </el-table-column> -->
                    <!-- <el-table-column align="center" prop="idType" label="身份证/驾驶证" :formatter="formatIdType" width="150">
                    </el-table-column>
                    <el-table-column align="center" prop="idPicture" label="证件照">
                        <template scope="scope">
                            <el-button type="text" @click="showPic(scope.$index, scope.row)">预览</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="carInfo" label="车辆信息" width="100">
                        <template scope="scope">
                            <el-button type="text" @click="showCarInfo = false,editItem(scope.$index, scope.row)">查看详情</el-button>
                        </template>
                    </el-table-column> -->
                    <el-table-column align="center" label="操作" width="180">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="showCarInfo = true,editItem(scope.$index, scope.row)">查看详情</el-button>
                            <el-button size="small" type="danger" @click="operationStatus(scope.$index, scope.row)" :disabled="scope.row.userStatus == 3">{{scope.row.userStatus == 3? '已驳回': '驳　回'}}</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" size="large" title="查看详情" :visible.sync="editDialog" @close="closeDialog">
            <div class="info-content">
                <!-- 个人信息 -->
                <div v-if="showCarInfo">
                    <div class="avatar">
                        <img :src="eForm.avater">
                        <p>头像</p>
                    </div>
                    <el-row>
                        <el-col>用户ID：<span>{{eForm.id}}</span></el-col>
                        <el-col>姓名：<span>{{eForm.userName}}</span></el-col>
                        <el-col>性别：<span>{{formatGender(eForm)}}</span></el-col>
                        <el-col>手机号：<span>{{eForm.userPhone}}</span></el-col>
                        <el-col>绑定卡号：<span>{{eForm.idNumber}}</span></el-col>
                        <el-col>绑定信息：<!-- <span>{{eForm.idType == 1? '身份证': eForm.idType == 2? '驾照': '未知'}}</span> --></el-col>
                        <el-col>
                            信用：<span>{{eForm.creditScore}}</span>
                        </el-col>
                        <el-col>
                            账户余额：
                            <span>{{eForm.balance}}</span>
                        </el-col>
                        <el-col>星座：<span>{{eForm.constellation}}</span></el-col>
                        <el-col>驾龄：<span>{{eForm.driving}}</span></el-col>
                    </el-row>
                </div>
                <!-- 车辆信息 -->
                <el-row>
                    <el-col>
                        上传车辆：<span>{{eForm.phone}}</span>
                    </el-col>
                    <el-col :span="8">驾驶时长：<span>{{eForm.phone}}</span></el-col>
                    <el-col :span="8">驾驶车型数：<span>{{eForm.phone}}</span></el-col>
                    <el-col :span="8">历史总成交：<span>{{eForm.phone}}</span></el-col>
                    <el-col :span="8">驾驶次数：<span>{{eForm.phone}}</span></el-col>
                    <el-col :span="8">平均响应时间：<span>{{eForm.phone}}</span></el-col>
                    <el-col :span="8">平均接单量：<span>{{eForm.phone}}</span></el-col>
                    <el-col>注册时间：<span>{{formatTime(eForm)}}</span></el-col>
                    <el-col :span="8">最后一次登录时间：<span>{{getTime(eForm.updateAt && eForm.updateAt.time)}}</span></el-col>
                    <el-col :span="8">登录次数：<span>{{eForm.loginCount}}</span></el-col>
                    <el-col>身份验证：<span>{{eForm.phone}}</span></el-col>
                    <el-col>身份证/驾驶证：<span>{{eForm.idType == 1 ? '身份证' : eForm.idType == 2 ? '驾照' : '未知'}}</span></el-col>
                    <el-col>照片：</el-col>
                    <el-col><img class="idcard-pic" :src="eForm.idPicture"/></el-col>
                </el-row>
            </div>
            <div class="info-radio">
                <el-radio-group v-model="eForm.userStatus">
                    <el-radio class="radio" :label="1">通过</el-radio>
                    <el-radio class="radio" :label="2">拉黑</el-radio>
                    <el-radio class="radio" :label="3">驳回</el-radio>
                    <el-radio class="radio" :label="4">冻结</el-radio>
                </el-radio-group>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editSave()">确 定</el-button>
            </div>
        </el-dialog>

        <el-dialog :visible.sync="viewPicDialog" class="view-pic">
            <img :src="picUrl">
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            nameWord: '',
            idWord: '',
            searchName: '',
            searchId: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            editDialog: false, //弹出框-编辑
            eForm: {
                userStatus: 1,
                createAt: {}
            },
            //详情显示个人信息
            showCarInfo: false,
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            //预览图
            viewPicDialog: false,
            picUrl: null
        }
    },
    props: ['API'],
    methods: {
        search(ev) {
            this.nameWord = this.searchName
            this.idWord = this.searchId
            this.currentPage = 1
            this.getList(this.nameWord, this.idWord);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.nameWord, this.idWord)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化时间
            getTime(time) {
                return this.$common.getNowTime(time)
            },
            // 格式化状态
            formatIdType(row, column) {
                return row.idType == 1 ? '身份证' : row.idType == 2 ? '驾照' : '未知'
            },
            // 格式化身份
            formatUserType(row, column) {
                return row.userType == 1 ? '车东' : row.userType == 2 ? '车友' : '未知'
            },
            // 格式化性别
            formatGender(row, column) {
                return row.gender == 1 ? '男' : row.gender == 2 ? '女' : ''
            },
        // 查看-编辑
        editItem(index, row) {
            this.editDialog = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "user_uuid": row.userUuid
                }
            }
            this.$common._post('user/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data.user)
            })
        },

        // 编辑保存
        editSave() {
            this.$confirm('是否确认修改?', '提示', {
              type: 'warning'
            }).then(() => {
                var json = {
                    "head": {
                        "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                        "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                    },
                    "body": {
                       "user_uuid": this.eForm.userUuid,
                       "user_status": this.eForm.userStatus
                    }
                }
                this.$common._post('user/operationStatus', json, this, data => {
                    this.editDialog = false
                    this.$message({ type: 'success',message: data.description })
                    this.getList()
                })
            }).catch(() => {})
        },
        // 驳回
        operationStatus(index, row) {
            this.$confirm('是否驳回?', '提示', {
                type: 'warning'
            }).then(() => {
                var json = {
                    "head": {
                        "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                        "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                    },
                    "body": {
                       "user_uuid": row.userUuid,
                       "user_status": 3
                    }
                }
                this.$common._post('user/operationStatus', json, this, data => {
                    this.$message({
                        type: 'success',
                        message: '驳回成功!'
                    })
                    this.getList()
                })
            }).catch(() => { })
        },
        // 关闭详情弹窗时
        closeDialog() {
            this.showCarInfo = false
        },
        // 预览图片
        showPic(index, row) {
            this.viewPicDialog = true
            this.picUrl = this.tableData[index].pic
        },
        // 导出Exc表格
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelUserCredit', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.nameWord, this.idWord);
        },
        // 获取列表
        getList(userName = '', userId = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "userName": userName,
                   "id": userId,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('user/userList', json, this, data => {
                this.total = data.total
                this.tableData = data.userList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
    }
}
</script>

<style scoped lang="scss">
.dialog-info .info-content {
    position: relative;
    .idcard-pic {
        max-width: 200px;
        vertical-align: middle;
    }
    .avatar {
        img {
            width: 120px;
            height: 120px;
        }
        position: absolute;
        right: 60px;
        text-align: center;
    }
}

.view-pic {
    text-align: center
}

.blue,
.green {
    background: rgba(19,206,102,.12);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    span {
        position: absolute;
        transform: translate(-50%, -50%);
        top: 50%;
        left: 50%;
    }
}
.blue {
    background: rgba(32,160,255,.25);
}
</style>



